<template>
  <div>
    <phlix-navbar></phlix-navbar>

    <div v-if="isSignedIn" class="container is-fluid">
      <h1 class="title">Your watchlist</h1>
      <div class="tabs">
        <ul>
          <li :class="{ 'is-active': showFirstTab }" @click="showFirstTab = true">
            <a>Movies</a>
          </li>
          <li :class="{ 'is-active': !showFirstTab }" @click="showFirstTab = false">
            <a>TV shows</a>
          </li>
        </ul>
      </div>

      <div style="height: max-content" v-show="showFirstTab">
        <div class="columns is-multiline is-mobile">
          <watchlistcard
            v-for="(movie, index) in movies"
            :info="movie"
            :key="index"
            type="movies"
            @deletemovie="deletemovie($event)"
            @rate="rate($event)"
          ></watchlistcard>
        </div>
        <infinite-loading @infinite="infiniteMovieHandler" v-if="!moviefirstrun">
          <div slot="no-results">
            <p class="subtitle is-5" style="padding-top:3rem; padding-bottom:3rem">No more items</p>
          </div>
          <div slot="no-more">
            <p class="subtitle is-5" style="padding-top:3rem; padding-bottom:3rem">No more items</p>
          </div>
        </infinite-loading>
      </div>

      <div style="height: max-content" v-show="!showFirstTab">
        <div class="columns is-multiline is-mobile">
          <watchlistcard
            v-for="(tvshow, index) in tv"
            :info="tvshow"
            :key="index"
            type="tv"
            @deletetv="deletetv($event)"
            @rate="rate($event)"
          ></watchlistcard>
        </div>
        <infinite-loading @infinite="infiniteTvHandler" v-if="!tvfirstrun">
          <div slot="no-results">
            <p class="subtitle is-5" style="padding-top:3rem; padding-bottom:3rem">No more items</p>
          </div>
          <div slot="no-more">
            <p class="subtitle is-5" style="padding-top:3rem; padding-bottom:3rem">No more items</p>
          </div>
        </infinite-loading>
      </div>
    </div>

    <div v-else class="container is-fluid" style="display: flex; justify-content: center; margin: auto">
      <p class="subtitle is-3" style="padding-top: 10rem">Connect to TMDb to access your watchlist</p>
    </div>
    <!-- :identifier="infiniteId" -->
    <rating
      v-if="showRating"
      :ratingdata="ratingdata"
      @closerating="showRating = false; ratingdata = {}"
    ></rating>
  </div>
</template>

<script>
import Navbar from "@/components/Navbar.vue";
import Cards from "@/components/Cards.vue";
import Card from "@/components/Card.vue";
import InfiniteLoading from "vue-infinite-loading";
import Watchlistcard from "@/components/Watchlistcard.vue";
import TMDb from "@/utils/TMDb.js";
import Rating from "@/components/Rating.vue";
import { Bus } from "@/utils/Bus.js";


const tmdb = new TMDb();
Window.tmdb = tmdb;

export default {
  name: "watchlist",
  data() {
    return {
      showFirstTab: true,
      showRating: false,
      ratingdata: {},
      watchlist: [],
      movies: [],
      tv: [],
      moviepage: 1,
      movietotalpages: 1,
      moviefirstrun: true,
      tvfirstrun: true,
      tvpage: 1,
      tvtotalpages: 1,
      isSignedIn: false
    };
  },
  methods: {
    fetchMovieWatchlistPage(page = 1, from) {
      return new Promise((resolve, reject) => {
        tmdb
          .getMovieWatchlist(page)
          .then(resp => {
            resp.data.results.forEach(movie => this.movies.push(movie));
            this.moviepage = resp.data.page;
            this.movietotalpages = resp.data.total_pages;
            this.moviefirstrun = false;
            console.log("Fetched movie page " + page + " from " + from);
            resolve();
          })
          .catch(error => {
            console.error(error);
            reject();
          });
      });
    },
    fetchTvWatchlistPage(page = 1) {
      return new Promise((resolve, reject) => {
        tmdb
          .getTvWatchlist(page)
          .then(resp => {
            resp.data.results.forEach(tv => this.tv.push(tv));
            this.tvpage = resp.data.page;
            this.tvtotalpages = resp.data.total_pages;
            this.tvfirstrun = false;
            console.log("Fetched tv page " + page);
            resolve();
          })
          .catch(error => {
            console.error(error);
            reject();
          });
      });
    },
    infiniteMovieHandler($state) {
      if (this.showFirstTab) {
        //movies
        if (
          this.moviepage >= this.movietotalpages &&
          this.moviefirstrun == false
        ) {
          console.log("Finished loading all movies");
          $state.complete();
        } else {
          this.fetchMovieWatchlistPage(
            this.moviepage + 1,
            "infiniteMovieHandler"
          ).then(() => {
            console.log("Finished loading movie page " + this.moviepage);
            $state.loaded();
          });
        }
      }
    },
    infiniteTvHandler($state) {
      if (!this.showFirstTab) {
        //tv
        if (this.tvpage >= this.tvtotalpages && this.tvfirstrun == false) {
          console.log("Finished loading all tv shows");
          $state.complete();
        } else {
          this.fetchTvWatchlistPage(this.tvpage + 1).then(() => {
            console.log("Finished loading tv page " + this.tvpage);
            $state.loaded();
          });
        }
      }
    },
    deletemovie(id) {
      this.movies = this.movies.filter(movie => movie.id != id);
    },
    deletetv(id) {
      this.tv = this.tv.filter(tv => tv.id != id);
    },
    rate(data) {
      this.ratingdata = data;
      this.showRating = true;
    }
  },
  created() {
    Bus.$on("signedoff", (() => this.isSignedIn = false));
    this.isSignedIn = tmdb.isSignedIn();
    if (this.isSignedIn) {
      this.fetchMovieWatchlistPage(1, "created");
      this.fetchTvWatchlistPage();
    }
  },
  components: {
    "phlix-navbar": Navbar,
    Card,
    Cards,
    InfiniteLoading,
    Watchlistcard,
    Rating
  }
};
</script>

